import { useUpdate } from "hooks/useUpdate"
import { createId } from "lib/createId"
import { useEffect, useRef, useState } from "react"

const defaultTags = [
    { id: createId(), name: '衣' },
    { id: createId(), name: '食' },
    { id: createId(), name: '住' },
    { id: createId(), name: '行' }
]

const useTags = () => {
    const [tags, setTags] = useState<{ id: number; name: string }[]>([])

    // 生命周期：after mount 
    useEffect(() => {
        // 读操作：
        // 持久化操作：第一次进入页面从 缓存中读取数据。
        let loaclTags = JSON.parse(window.localStorage.getItem('tags') || '[]')
        console.log("loaclTags1", loaclTags)
        if (loaclTags.length === 0) {
            loaclTags = defaultTags
            console.log("loaclTags2", loaclTags)

            console.log('fuck-c1')
        }
        console.log('fuck-a')
        setTags(loaclTags) // 类似于 setState,会再刷新，所有的页面
    }, [])

    // useUpdate 的作用就是阻止第一次默认渲染
    useUpdate(() => {
        console.log('set item')
        window.localStorage.setItem('tags', JSON.stringify(tags))
    }, [tags])

    // 正常使用：
    useEffect(() => {
        // 写操作。具体的过程是 ： tag：undefined => [] =>
        console.log('fuck')
        window.localStorage.setItem('tags', JSON.stringify(tags))
    }, [tags])


    // 过滤出符合条件的数组
    const findTag = (id: number) => tags.filter(tag => tag.id === id)[0]
    // 封装一个符合条件的索引
    const findTagIndex = (id: number) => {
        let result = -1;
        for (let i = 0; i < tags.length; i++) {
            if (tags[i].id === id) {
                result = i;
                break;
            }
        }
        return result;
    }

    // 提供一个新的更新 tag 的接口：第1个参数：修改的 id | 第2个参数：修改内容
    const updateTag = (id: number, obj: { name: string }) => {
        const index = findTagIndex(id);
        // 原封不动的把 tags 数组拷贝一份，得到tagsClone
        const tagsClone = JSON.parse(JSON.stringify(tags))
        // 使用 slice 删除原数组
        tagsClone.splice(index, 1, { id: id, name: obj.name })
        // 依然使用setTags进行数据的更新：从这可以看出updateTag是基于setTags进一步封装
        setTags(tagsClone)
    }

    // 提供删除接口
    const deleteTag = (id: number) => {
        setTags(tags.filter(tag => tag.id !== id))
    }

    // 提供添加 tag 的接口
    const addTag = () => {
        const tagName = window.prompt("请输入tag名称")
        if (tagName !== null) {
            setTags([...tags, { id: createId(), name: tagName }])
        }
    }

    return { tags, addTag, setTags, findTag, updateTag, deleteTag }
}

export { useTags }