import Icon from "components/Icon"
import { Input } from "components/Input"
import Layout from "components/Layout"
import Nav from "components/Nav"
import { Fragment } from "react"
import { useParams } from "react-router"
import styled from "styled-components"
import { useTags } from "useTags"
import { Button } from "./Tags/Button"
import { Center } from "./Tags/Center"
import { Space } from "./Tags/Space"

type Params = {
    tagId: string
}

const TopBar = styled.header`
    display:flex;
    justify-content: space-between;
    align-items: center;
    line-height: 20px;
    padding: 14px;
    background: white;
    border-bottom: 1px solid #000;
`
const InputWrapper = styled.div`
    background: #cccff3;
    padding: 16px 10px;
    margin-top: 2px;
    > label > span {
        padding: 0 10px;
    }
`

const Tag: React.FC = () => {
    // tags 是 {id: number, name: string}
    const { findTag, updateTag,deleteTag } = useTags()
    // 解构传入的参数
    const { tagId } = useParams<Params>()
    // 筛选出符合条件的标签
    const tag = findTag(parseInt(tagId))
    // 回退功能：
    const onClickBack = ()=>{
        window.history.back()
    } 

    return (
        <Fragment>
            <Layout>
                <TopBar>
                    <Icon name="right" onClick={onClickBack}></Icon>
                    <span>编辑标签</span>
                    <Icon></Icon>
                </TopBar>
                <InputWrapper>
                    <Input type="text" label="备注" value={tag.name} onChange={(e) => {
                        // 从打印可以看出，tag.name 确实改变了，但是页面视图却没有改变。
                        console.log(e.target.value)
                        updateTag(tag.id, { name: e.target.value })
                    }} />
                </InputWrapper>
                <Space />
                <Space />
                <Space />
                <Center>
                    <Button onClick={()=>{ deleteTag(tag.id) }}>删除标签</Button>
                </Center>
            </Layout>
            <Nav />
        </Fragment>
    )
}

export { Tag }