import { useEffect, useState } from "react"
import { useUpdate } from "./useUpdate"
// 自定义 hooks： 用于 onOk

// 需要存入的是recordItem： 
// 备注信息，所有选中的表圈，是存钱还是花钱，具体的金额是多少？

type RecordItem = {
    tagIds: number[]
    note: string
    category: '+' | '-'
    amount: number
}

type newRecordItem = RecordItem & {
    createAt?: string
}

export const useRecords = () => {
    const [records, setRecords] = useState<RecordItem[]>([])

    const addRecord = (record: RecordItem) => {
        console.log('fuck2')
        setRecords([...records, record])
    }

    // 新的 addRecord
    const newAddRecord = (newRecord: RecordItem) => {
        // 多加上一个属性
        const record = { ...newRecord, createAt: (new Date()).toISOString() }
        // 将这项 加入 记录数组records
        setRecords([...records, record])
    }

    // 使用生命周期函数，刚开始的时候从缓存中读取变量
    useEffect(() => {
        console.log('生命周期1')
        setRecords(JSON.parse(window.localStorage.getItem('records') || '[]'))
    }, [])

    // 使用生命周期函数存 record , 通过监听records
    useUpdate(() => {
        console.log('生命周期2')
        window.localStorage.setItem('records', JSON.stringify(records))
    }, [records])

    // 这部分没有作用
    useEffect(() => {
        console.log('生命周期2')
        window.localStorage.setItem('records', JSON.stringify(records))
    }, [records])

    return { records, addRecord, newAddRecord }
}