import { useEffect, useRef } from "react"


export const useUpdate = (fn:()=>void , deps: any[]) => {
    let count = useRef(0)
    // 监听render的次数，如果render了就让count.current+1.
    useEffect(() => {
        count.current += 1
    }, [])
    // 生命周期：监听tags的变化
    useEffect(() => {
        if (count .current > 1) {
            console.log('set item')
            fn()
        }
    }, deps)
}
